<template>
  <div>
    <form name="aspnetForm" method="post" action="Gift.aspx?id=13" id="aspnetForm">
      <div>
        <input
          type="hidden"
          name="__VIEWSTATE"
          id="__VIEWSTATE"
          value=""
        >
      </div>

      <div>
        <input
          type="hidden"
          name="__EVENTVALIDATION"
          id="__EVENTVALIDATION"
          value="/wEWAgLgkbuuCQKI+JrmBf+Ul372cpCvOJOEI7fXW4euRKUJ"
        >
      </div>
      <div id="ctl00_ContentPlaceHolder1_Panel1">
        <div class="container width90">
          <h3>
            <span id="ctl00_ContentPlaceHolder1_txtGiftName">{{this.name}}</span>
          </h3>
          <p>所需积分：
            <span id="ctl00_ContentPlaceHolder1_txtGiftCost">{{this.scoer}}</span>
          </p>
          <p>
            <img
              style="display: block;"
              src="../assets/Images/calendar.png"
              height="300"
              width="300"
            >
          </p>
          <p></p>
          <p style="text-indent:0px;color:#999999;">
            <em>
              <strong>产品参数：</strong>
            </em>
          </p>
          <ul style="text-indent:0px;color:#404040;">
            <li style="color:#666666;vertical-align:top;">品牌:&nbsp;emie/亿觅。</li>
            <li style="color:#666666;vertical-align:top;">型号:&nbsp;emie萨摩移动电源</li>
            <li
              style="color:#666666;vertical-align:top;"
            >颜色分类:&nbsp;黄色萨摩&nbsp;白色萨摩+香港潮集充电头&nbsp;黄色萨摩+香港潮集充电头&nbsp;白色萨摩</li>
            <li style="color:#666666;vertical-align:top;">电池容量:&nbsp;5001MAh(含)-6000MAh(含)</li>
            <li style="color:#666666;vertical-align:top;">是否支持太阳能:&nbsp;否</li>
            <li style="color:#666666;vertical-align:top;">适用类型:&nbsp;通用</li>
            <li style="color:#666666;vertical-align:top;">LED灯照明:&nbsp;否</li>
            <li style="color:#666666;vertical-align:top;">电池类型:&nbsp;锂电池</li>
          </ul>
          <p>&nbsp;</p>
          <div class="control-group tc">
            <p class="red"></p>
            <input
              type="submit"
              name="ctl00$ContentPlaceHolder1$btnOK"
              value="立即兑换"
              id="ctl00_ContentPlaceHolder1_btnOK"
              class="btn-large green button width80"
              style="width:80%;"
            >
          </div>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  data(){
    return {
      name:'',
      scoer:''
    }
  },
 mounted() {
   axios({
      url:'/data/giftlist.json'
    }).then(
      res=>{
        res.data.forEach((item)=> {
          if(item.id == this.$route.params.id){
            this.name = item.name
            this.scoer = item.scoer
          }
        });  
        
        }
    )
    this.$root.title = '礼品详情'
 },
 
}
</script>